.litros {	
margin: 5%;

}
.litros {
background-color: red;
background-size: contain;
color: white;
justify-content: center;
align-items: center;
text-align: center;
display: flex;
padding: 1%;
}


#parteinferior {
	display: grid;
	grid-template-columns: 50% 50%;
     justify-content: center;
     align-items: center;
}
#parte_superior img {
	width: 5vw;
	height: 10vh;
	margin: 5%;
}
.litros button {
	 background-color: #3BC37B;
   color: white;
	  border-radius: 10%;
   border: none;
   width: 10vw;
   height: 7vh;
}
#parte_superior {
	display: grid;
	grid-template-columns: 6% 6% 80%;
	text-align: center;
	margin: 2%;

}
